import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import moment from "moment";
import NumberFormat from "../../../App/NumberFormat";

import notification from "../../../../components/notification";
import {
  Empty,
  Collapse,
  Select,
  DatePicker,
  Row,
  Col,
  Divider,
  InputNumber
} from "antd";

// import Popconfirms from "../../../../components/feedback/popconfirm";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Scrollbars from "../../../../components/utility/customScrollBar";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/report/topReceivedItemReport/actions";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import CardWrapper, { Box, customCollapseStyle } from "../../../App/app.style";
import PrintWrapper from "../../../App/PrintWrapper";
import ReactToPrint from "react-to-print";
import ButtonPrint from "../../../App/ButtonPrint";
import ExcelWrapper from "../../../App/ExcelWrapper";
import PDFWrapper from "../../../App/PDFWrapper";
import getLanguage from "../../../App/MyLanguange";
import { getFileName } from "../../../../helpers/helper";
import { siteConfig } from "../../../../settings";

const Panel = Collapse.Panel;
const Option = Select.Option;
const { RangePicker } = DatePicker;

const redirectTo = "/dashboard/report-menus";

class TopReceivedItemReport extends Component {
  state = {
    filter: {
      id_location: null,
      sort_type: "desc",
      limit: 10,
      start_date: moment()
        .startOf("month")
        .format("YYYY-MM-DD HH:mm:ss"),
      end_date: moment().format("YYYY-MM-DD HH:mm:ss")
    }
  };

  // Get All data diawal
  componentDidMount() {
    const { getTopReceivedItemReports } = this.props;
    getTopReceivedItemReports();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.topReceivedItemReport.error) {
      notification("error", this.props.topReceivedItemReport.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.topReceivedItemReport.success) {
      notification("success", this.props.topReceivedItemReport.success);
      this.props.clearSuccess();
    }
  }

  //   Handle filter change
  handleFilterChange = (name, value) => {
    if (name === "date" && value[0] && value[1]) {
      const start = value[0].format("YYYY-MM-DD HH:mm:ss");
      const end = value[1].format("YYYY-MM-DD HH:mm:ss");
      // console.log(start, end);
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: start,
          end_date: end
        }
      });
    } else if (name === "date" && !value[0] && !value[1]) {
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: null,
          end_date: null
        }
      });
    } else {
      this.setState({
        filter: {
          ...this.state.filter,
          [name]: value
        }
      });
    }
  };

  filterValue = () => {
    const { filter } = this.state;
    let string = "";

    if (filter.sort_type) {
      string += "sort_type=" + filter.sort_type;
    }
    if (filter.limit) {
      string
        ? (string += "&limit=" + filter.limit)
        : (string += "limit=" + filter.limit);
    }
    if (filter.start_date && filter.end_date) {
      string
        ? (string +=
            "&start_date=" + filter.start_date + "&end_date=" + filter.end_date)
        : (string +=
            "start_date=" + filter.start_date + "&end_date=" + filter.end_date);
    }
    if (filter.id_location) {
      string
        ? (string += "&id_location=" + filter.id_location)
        : (string += "id_location=" + filter.id_location);
    }

    return string;
  };

  // Handle filter
  handleFilter = () => {
    const { getTopReceivedItemReports } = this.props;
    //const { current_page } = this.props.topReceivedItemReport;
    let filter = this.filterValue();

    getTopReceivedItemReports(1, null, filter, this.state.filter);
  };

  // Handle Reset Filter
  handleResetFilter = () => {
    const { getTopReceivedItemReports } = this.props;
    this.setState({
      filter: {
        id_location: null,
        sort_type: "desc",
        limit: 10,
        start_date: moment()
          .startOf("month")
          .format("YYYY-MM-DD HH:mm:ss"),
        end_date: moment().format("YYYY-MM-DD HH:mm:ss")
      }
    });
    getTopReceivedItemReports();
  };

  render() {
    // console.log(this.props);

    // Data setup
    const {
      top_received_item_reports,
      locations,
      filter,
      filter_select
    } = this.props.topReceivedItemReport;
    //console.log(this.props.topReceivedItemReport.base_currency); //console.log(this.state);

    // Definisi kolom2 serta field2 API yang dipakai tabel
    const columns = [
      {
        title: getLanguage({ id: "no." }),
        dataIndex: "key",
        rowKey: "key",
        width: "5%",
        align: "center"
      },
      {
        title: getLanguage({ id: "itemCode" }),
        dataIndex: "item_code",
        rowKey: "item_code",
        width: "20%"
      },
      {
        title: getLanguage({ id: "itemName" }),
        dataIndex: "item_name",
        rowKey: "item_name",
        width: "20%"
      },
      {
        title: getLanguage({ id: "sidebar.location" }),
        dataIndex: "location_name",
        rowKey: "location_name",
        width: "20%"
      },
      {
        title: getLanguage({ id: "totalQuantity" }),
        dataIndex: "total_qty",
        rowKey: "total_qty",
        width: "35%",
        align: "right",
        parse: true,
        render: text => <NumberFormat value={parseFloat(text)} />
      }
    ];

    const dataSource = [];

    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    top_received_item_reports.map((data, key) => {
      const modifier = { key: key + 1 };

      dataSource.push({
        ...data,
        ...modifier
      });
    });

    const { loading, loadingForeign } = this.props.topReceivedItemReport;

    const fileName = getLanguage({ id: "topReceivedItemReports.pageHeader" })
    // console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="topReceivedItemReports.pageHeader" />}
            marginBottom={true}
          >
            <PrintWrapper
              componentRef={ref => (this.componentRef = ref)}
              title={<IntlMessages id="topReceivedItemReports.pageHeader" />}
              columns={columns}
              data={dataSource}
              filter={filter_select}
              locations={locations}
              filterLocation="id_location"
            />

            <ReactToPrint
              trigger={() => <ButtonPrint loading={loading} />}
              content={() => this.componentRef}
              onBeforePrint={() => (document.title = getFileName(fileName, null, false))}
              onAfterPrint={() => document.title = siteConfig.documentTitle}
            />

            <PDFWrapper
              columns={columns}
              data={dataSource}
              fileName={fileName}
              filter={filter_select}
              loading={loading}
              locations={locations}
              scale={0.6}
              title="topReceivedItemReports.pageHeader"
            />

            <ExcelWrapper
              title="topReceivedItemReports.pageHeader"
              fileName={fileName}
              loading={loading}
              columns={columns}
              data={dataSource}
              filter={filter_select}
              locations={locations}
              filterLocation="id_location"
            />

            <Button
              style={{ marginLeft: "5px" }}
              onClick={() => this.props.history.replace(redirectTo)}
            >
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <div className="isoFilter">
            <Collapse style={customCollapseStyle} defaultActiveKey={["1"]}>
              <Panel header={<IntlMessages id="filter.header" />} key="1">
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "10px" }}
                >
                  <Col span={12}>
                    <label>
                      <IntlMessages id="locations.pageHeader" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_location"
                      )}
                      value={
                        this.state.filter.id_location
                          ? this.state.filter.id_location
                          : []
                      }
                      style={{ width: "95%" }}
                      placeholder={<IntlMessages id="select.location" />}
                      optionFilterProp="children"
                      filterOption={true}
                      loading={loadingForeign}
                      allowClear={true}
                      //showArrow={false}
                    >
                      {locations.map((data, key) => (
                        <Option key={key} value={data.id_location}>
                          {`${data.location_code} (${data.location_name})`}
                        </Option>
                      ))}
                    </Select>
                  </Col>

                  <Col span={12}>
                    <label>
                      <IntlMessages id="salesOrders.filter.date" />
                    </label>
                    <br />
                    <RangePicker
                      format="DD MMMM YYYY HH:mm:ss"
                      showTime
                      allowClear={false}
                      style={{ width: "100%" }}
                      value={
                        this.state.filter.start_date &&
                        this.state.filter.end_date
                          ? [
                              moment(this.state.filter.start_date),
                              moment(this.state.filter.end_date)
                            ]
                          : []
                      }
                      onChange={this.handleFilterChange.bind(this, "date")}
                    />
                  </Col>
                </Row>
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "10px" }}
                >
                  <Col span={12}>
                    <Row justify="start" gutter={1} type="flex">
                      <Col span={8}>
                        <label>
                          <IntlMessages id="sortType" />
                        </label>
                        <br />
                        <Select
                          showSearch
                          onChange={this.handleFilterChange.bind(
                            this,
                            "sort_type"
                          )}
                          value={
                            this.state.filter.sort_type
                              ? this.state.filter.sort_type
                              : "desc"
                          }
                          style={{ width: "95%" }}
                          // placeholder={
                          //   <IntlMessages id="stockValuationReports.sortBy.select" />
                          // }
                          optionFilterProp="children"
                          filterOption={true}
                          //allowClear={true}
                          //showArrow={false}
                        >
                          <Option key={1} value="asc">
                            <IntlMessages id="less" />
                          </Option>
                          <Option key={2} value="desc">
                            <IntlMessages id="top" />
                          </Option>
                        </Select>
                      </Col>
                      <Col span={8}>
                        <label>
                          <IntlMessages id="numberOfItem" />
                        </label>
                        <br />
                        <InputNumber
                          onChange={this.handleFilterChange.bind(this, "limit")}
                          value={
                            this.state.filter.limit
                              ? this.state.filter.limit
                              : []
                          }
                          min={1}
                          precision={0}
                          style={{ width: "95%" }}
                        />
                      </Col>

                      <Col
                        span={8}
                        style={{
                          marginTop: "auto"
                        }}
                      >
                        <Button
                          size="small"
                          type="primary"
                          style={{ marginRight: "10px" }}
                          onClick={this.handleFilter}
                        >
                          <IntlMessages id="filter.only" />
                        </Button>
                        <Button onClick={this.handleResetFilter} size="small">
                          <IntlMessages id="filter.reset" />
                        </Button>
                      </Col>
                    </Row>
                  </Col>
                </Row>
              </Panel>
            </Collapse>
          </div>

          <CardWrapper>
            {!filter ? (
              <Empty description={<IntlMessages id="clickFilter" />} />
            ) : top_received_item_reports.length === 0 &&
              !this.props.topReceivedItemReport.loading ? (
              <Empty
                description={
                  <IntlMessages id="topReceivedItemReports.table.noData" />
                }
              />
            ) : (
              <div className="isoInvoiceTable">
                <Divider orientation="left" style={{ fontSize: 14 }}>
                  <IntlMessages id="details" />
                </Divider>
                <Scrollbars style={{ width: "100%" }}>
                  <TableWrapper
                    dataSource={dataSource}
                    columns={columns}
                    loading={loading}
                    className="invoiceListTable"
                    bordered={true}
                    // Set pagination, jika ada page_size mengunakan pagination API
                    // jika tidak ada page_size menggunakan pagination bawaan
                    pagination={false}
                  />
                </Scrollbars>
              </div>
            )}
          </CardWrapper>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    topReceivedItemReport: state.topReceivedItemReport,
    auth: state.auth
  };
};

export default compose(connect(mapStateToProps, actions))(
  TopReceivedItemReport
);
